<template>
  <div class="auth-detail-wrapper">
        <div class='auth-back-btn'> 
            <el-button @click='toIndex'></el-button>
        </div>
        <section class='auth-detail-main'>
            <!--  -->
            <div class='auth-detail-title'>
                <span v-if='type === "1"'></span>
                <span v-if='type === "2"'></span>
                <span v-if='type === "3"'></span>
            </div>
            <!--  -->
            <div class='block-wrapper'>
                <userInfo :value='detailData' :infoType='type'></userInfo>
            </div>
            <!-- ====  -->
            <!--  -->
            <div 
                class='block-wrapper hospital-wrapper'
                v-if='type === "1"'
            >
                <hospitalInfo :value='detailData' :infoType='type'></hospitalInfo>
            </div>
            <!--  -->
            <div 
                class='block-wrapper certi-wrapper'
                v-if='type === "1"'
            >
                <certiInfo :value='detailData' :infoType='type'></certiInfo>
            </div>
            <!-- ====== -->
            <!--  -->
            <div 
                class='block-wrapper intro-wrapper'
                v-if='type === "2"'
            >
                <div class="intro-title">:</div>
                <div>{{detailData.introduce || ''}}</div>
            </div>
            <!--  -->
            <div 
                class='block-wrapper'
                v-if='type === "2"'
            >
                <licenseInfo :value='detailData' :infoType='type'></licenseInfo>
            </div>
            <!--  -->
            <div 
                class='block-wrapper'
                v-if='type === "2"'
            >
               <ul class="block-ul clearfix" v-if='type === "2"'> 
                    <li>{{detailData.legalPerson || '-'}}</li>
                    <li>{{detailData.legalPersonPhone || '-'}}</li>
                    <li>{{detailData.legalPersonIdNumber || '-'}}</li>
                </ul>
            </div>
            <!--  -->
            <div 
                class='block-wrapper bb-none'
                v-if='type === "2"'
            >
               <ul class="block-ul clearfix" v-if='type === "2"'> 
                    <li>{{detailData.fullName || '-'}}</li>
                    <li>{{detailData.telePhone || '-'}}</li>
                    <li>{{detailData.gender || '-'}}</li>
                    <li>{{detailData.position || '-'}}</li>
                </ul>
            </div>

            <!-- =====  -->
            <!--  -->
            <div 
                class='block-wrapper deep-detail-wrapper'
                v-if='type === "3"'
            >
                <ul class="block-ul clearfix" v-if='type === "3"'> 
                    <li>{{detailData.gender || '-'}}</li>
                    <li>{{detailData.telephone || '-'}}</li>
                    <li>{{detailData.gender || '-'}}</li>
                    <li>{{detailData.country || '-'}}</li>
                </ul>
            </div>
            
        </section>
        <section class='btn-wrapper'>
            <el-button style="margin-right: 30px" @click="refuseCerti"></el-button>
            <el-button type="primary" @click="agreeCerti" :disabled="isDisabled"></el-button>
        </section>

        <!--  -->
        <el-dialog 
            class="grid-body"
            :visible.sync="dialogFormVisible"
            width="30%"
            @closed="handleClose"
            :close-on-click-modal='false'
        >
            <el-row>
                <el-form :model="form" ref="form" label-width="100px">
                    
                    <el-form-item 
                        label="" 
                        label-width='0px'
                        style="marginBottom: 0px"
                    >
                        <el-input 
                            class="w220" 
                            v-model.trim="form.reason" 
                            placeholder=""
                            type="textarea"
                            :rows="4" 
                            clearable>
                        </el-input>
                    </el-form-item>
                </el-form>
            </el-row>
            <div slot="footer" class="dialog-footer">
                
                <el-button @click="handleClose" size="small"></el-button>
                <el-button type="primary" @click="onSubmitdialog" size="small"></el-button>
            </div>
        </el-dialog>
  </div>
</template>

<script>
// @ is an alias to /src

import Api  from '@/api/index.js';
import userInfo from './components/userInfo.vue';
import hospitalInfo from './components/hospitalInfo.vue';
import certiInfo from './components/certiInfo.vue';
import licenseInfo from './components/licenseInfo';
import { debuglog } from 'util';

export default {
    name: "auth",
    components: {
		// HelloWorld
        userInfo,
        hospitalInfo,
        certiInfo,
        licenseInfo
    },
    data () {
        return {
            type: '',
            id: '',
            detailData: {
                
            },
            isDisabled: false,
            dialogFormVisible: false,
            form: {

            }
        }
    },
    created(){
        this.id = this.$route.query.id;
        this.type = this.$route.query.type;
        // console.log(this.$route.query)
    },
    mounted(){
        this.getDetailData()
    },
    methods: {
        getDetailData(){
            let _title = '';
            let _url = '';
            let _param = {};
            if(this.type === '1'){
                _title = ''
                _url = 'operateFindDoctorDetail'
                _param = {
                    doctorId: this.id
                }
            }else if(this.type === '2'){
                _title = ''
                _url = 'operateFindHospitalDetail'
                _param = {
                    hospitalId: this.id
                }
            }else if(this.type === '3'){
                _title = ''
                _url = 'operateFindConsultantDetail'
                _param = {
                    consultantId: this.id
                }
            }
            if(_url){
                Api[_url](_param)
                .then((res) =>{
                    console.log(res,'sss')
                    if(res && res.status === 200){
                        // 
                        if(res.data.result && res.data.result === 500){
                            // this.$router.push({
                            //     path: '/auth/authIndex',
                            //     query: {
                            //         // id,
                            //         // type 
                            //     }
                            // })
                            this.$message({
                                message: `${_title}`,
                                type: 'warning'
                            });
                        }else{
                            let _detailData = res.data;
                            // 
                            if(_detailData.BeGoodAteStr){
                                _detailData.BeGoodAteStr = _detailData.BeGoodAte.join('');
                            }
                            this.detailData = _detailData;
                        }
                        
                    }
                })
                .catch(function (error) {
                })
            }
            
        },
        toIndex(){
            this.$router.push({
                path: '/auth/authIndex',
                query: {
                    // id,
                    // type 
                }
            })
        },
        // 
        agreeCerti(){
            const { type, detailData } = this;
            let self = this;
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.isDisabled = true;
                let _title = '';
                let _url = '';
                if(type === '1'){
                    _title = ''
                    _url = 'updateOperateAgreeDoctor'
                }else if(type === '2'){
                    _title = ''
                    _url = 'updateOperateAgreeHospital'
                }else if(type === '3'){
                    _title = ''
                    _url = 'updateOperateAgreeConsultant'
                }
                if(_url){
                    Api[_url]({
                        userId: detailData.userId,
                    })
                    .then((res) =>{
                        this.isDisabled = false;
                        if(res && res.status === 200){
                            if(res.data.result){
                                this.onCallback(true)
                            }else{
                                this.$message({
                                    message: '',
                                    type: 'warning'
                                });
                            }
                            
                        }
                    })
                    .catch(function (error) {
                        self.isDisabled = false;
                    })
                }

                
            }).catch(() => {
                // this.$message({
                //     type: 'info',
                //     message: ''
                // });          
            });
        },
        onCallback(isSuccess){
            const { type } = this;
            if(isSuccess){
                this.$message({
                    message: "",
                    type: 'success'
                });
            }else{
                this.$message({
                    message: "",
                    type: 'success'
                });
            }

            this.$router.push({
                path: '/auth/authIndex',
                query: {
                    // id,
                    type 
                }
            })
        },
        //  
        refuseCerti(){
            this.dialogFormVisible = true;
        },
        handleClose(){

            this.dialogFormVisible = false;
            this.form.reason = '';
            this.$refs.form.resetFields();
        },
        onSubmitdialog(){
            const { type, detailData } = this;
            let { reason } = this.form;
            let params = {
                reason,
                userId: detailData.userId
            };
            let _title = '';
            let _url = '';
            if(type === '1'){
                _title = ''
                _url = 'updateOperateRefuseDoctor'
            }else if(type === '2'){
                _title = ''
                _url = 'updateOperateRefuseHospital'
            }else if(type === '3'){
                _title = ''
                _url = 'updateOperateRefuseConsultant'
            }
            if(_url){
                Api[_url](params)
                .then((res) =>{
                    if(res && res.status === 200){
                        if(res.data.result){
                            this.onCallback(false)
                        }
                    }
                })
                .catch((error) => {
                    this.dialogFormVisible = false;
                });
            }
        }
            
        
		
    }
};
</script>


<style lang='less' scoped>
@theme-color: #ff8232;
@text-color:#686868;
@border-color: rgba(221,221,221,1);
.auth-detail-wrapper{
    width: 810px;
    margin: 0 auto;
    // background: #f6f7f9;
}
.auth-back-btn{
    padding: 25px 0;
}
.auth-detail-main{
    color: @text-color;
    border:1px solid @border-color;
    .auth-detail-title{
        height: 60px;
        line-height: 60px;
        text-align: center;
        border-bottom: 1px solid @border-color;
    }
    .block-wrapper{
        padding: 15px 20px;
        border-bottom: 1px solid @border-color;
    }
    .hospital-wrapper{
        max-height: 280px;
        overflow-y: auto;
    }
    .certi-wrapper{
        border-bottom: none;
    }
    .block-ul{
        li{
            width: 33%;
            float:left;
            padding-bottom: 14px;
        }
        li.w50{
            width: 50%;
        }
    }
    .intro-wrapper{
        max-height: 154px;
        overflow-y: auto;
        .intro-title{
            padding-bottom: 10px;
        }
    }
    .bb-none{
        border-bottom: none;
    }
}
.btn-wrapper{
    padding: 40px 0;
    text-align: center;
}
</style>
